@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 100vh;
  background: rgb(255, 255, 255);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
}

body::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(rgb(1, 218, 30), rgb(0, 225, 255));
  -webkit-clip-path: circle(30% at right 70%);
          clip-path: circle(30% at right 70%);
}

body::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(#ffab4b, #f01313);
  -webkit-clip-path: circle(20% at 10% 10%);
          clip-path: circle(20% at 10% 10%);
}

.card{position:relative;width:250px;height:150px;margin:20px;box-shadow:20px 20px 50px rgba(0,0,0,0.5);border-radius:15px;background:rgba(255,255,255,0.1);overflow:hidden;display:flex;justify-content:center;align-items:center;border-top:1px solid rgba(255,255,255,0.5);border-left:1px solid rgba(255,255,255,0.5);-webkit-backdrop-filter:blur(5px);backdrop-filter:blur(5px)}.carddone{background:rgba(243,77,0,0.12)}.card .content{padding:10px;text-align:left;transition:0.5px}.card .content h1{position:absolute;top:-10px;right:30px;font-size:4em;font-weight:800;color:rgba(0,0,0,0.09);pointer-events:none}.card .content h3{font-size:1.2em;color:#000;z-index:1}.card .content p{font-size:0.7em;color:#000;font-weight:300}.card .content a{position:relative;display:inline-block;padding:8px 20px;margin-top:15px;background:#fff;color:#000;border-radius:20px;text-decoration:none;font-weight:500;box-shadow:0 5px 15px rgba(0,0,0,0.2)}.content{cursor:pointer}.content>.completed{font-style:italic;text-decoration:line-through;color:#ccc}.buttonGroup{display:flex;justify-content:center;align-items:center}.buttonFilter{box-shadow:inset 0px 1px 0px 0px #f6fadc;background:linear-gradient(to bottom, #faf0be 5%, #e8a97f 100%);background-color:#faf0be;border-radius:6px;border:1px solid #f29285;display:inline-block;color:#ffffff;font-family:Arial;font-size:15px;font-weight:bold;padding:6px 24px;text-decoration:none;text-shadow:0px 1px 0px #cc5454;position:relative;z-index:1;margin:5px;padding:5px}.buttonFilter:hover{background:linear-gradient(to bottom, #e8a97f 5%, #faf0be 100%);background-color:#e8a97f}.buttonFilter:active{position:relative;top:1px}

.container{position:relative;display:flex;justify-content:flex-start;align-items:center;max-width:1200px;flex-wrap:wrap;z-index:1}

